/* styles.css */

/* Modal Styles */
.ReactModal__Overlay {
    top: 0;
    z-index: 1000;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: rgba(0, 0, 0, 0.5);
    align-items: center;
    display: flex;
    justify-content: center;
  }
  
  .ReactModal__Content {
    z-index: 1000;
    position: relative;
    background: white;
    padding: 60px;
    border-radius: 8px;
    max-width: 80%;
    max-height: 80%;
    overflow: auto;
    text-align: center;
    margin: 0 auto;
    
  }
  
  /* Processing Circle Animation */
  .processing-circle {
    border: 4px solid rgba(0, 0, 0, 0.1);
    border-top: 4px solid #3498db;
    border-radius: 50%;
    width: 70px;
    height: 70px;
    animation: spin 1s linear infinite;
    margin: 0 auto;
  }
  
  @keyframes spin {
    0% {
      transform: rotate(0deg);
    }
    100% {
      transform: rotate(360deg);
    }
  }

  .cancel-button {
    
    background-color: #063f69;
  top: 10px;
    right: 10px;
  border: 1px solid transparent;
  border-radius: 3px;
  box-shadow: rgba(255, 255, 255, .4) 0 1px 0 0 inset;
  box-sizing: border-box;
  color: #fff;
  cursor: pointer;
  display: inline-block;
  font-family: -apple-system,system-ui,"Segoe UI","Liberation Sans",sans-serif;
  font-size: 13px;
  font-weight: 400;
  line-height: 1.15385;
  margin: 0;
  outline: none;
  padding: 8px .8em;
  text-align: center;
  text-decoration: none;
  user-select: none;
  -webkit-user-select: none;
  touch-action: manipulation;
  vertical-align: baseline;
  white-space: nowrap;
  }

  .modal-content {
    position: relative;
  }

  

/* CSS */


.cancel-button:hover,
.cancel-button:focus {
  background-color: #07c;
}

.cancel-button:focus {
  box-shadow: 0 0 0 4px rgba(0, 149, 255, .15);
}

.cancel-button:active {
  background-color: #0064bd;
  box-shadow: none;
}
.image{
  width: 100%;
  height: 325px;
  align-items: center;
  /* background-position: center; */
  /* background-image: cover; */
}
.find{
  font-size:smaller;
}
.flex-container {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.flex-spacer {
  flex: 1; /* This takes up the available space */
}
.error-message{
  background-color: chocolate;
  color: #fff;
  margin: auto;
  width: 50%;
  border-radius: 2px;
  padding: 2px, 2px, 2px, 2px;
}
.processing-box{
  position: absolute;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  padding: 10px;
}
